import styled from "styled-components";
import { Link } from "react-router-dom";

const Recommends = (props) => {
    return(
        <Container>
            <h4>Recommends for you </h4>
            <Content>
                <Wrap>
                    <Link>
                      <img src="https://cannonball-cdn.bamgrid.com/assets/originals/Star_Wars_TROS_EN.jpg"/>
                    </Link>
                </Wrap>

                <Wrap>
                    <Link>
                      <img src="https://cannonball-cdn.bamgrid.com/assets/originals/EN-US_phinFerb@1.5X.jpg"/>
                    </Link>
                </Wrap>

                <Wrap>
                    <Link>
                      <img src="https://cannonball-cdn.bamgrid.com/assets/originals/Onward_en-US.jpg"/>
                    </Link>
                </Wrap>

                <Wrap>
                    <Link>
                      <img src="https://cannonball-cdn.bamgrid.com/assets/originals/AvengersEndgame@2x-2.png"/>
                    </Link>
                </Wrap>

            </Content>
        </Container>
    );
};

const Container = styled.div`
  padding: 0 0 26px;
`;

const Content = styled.div`
  display: grid;
  grid-gap: 25px;
  gap: 25px;
  grid-template-columns: repeat(4, minmax(0, 1fr));

  @media (max-width: 768px){
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
`;

const Wrap = styled.div`
 padding-top: 56.25%;
 border-radius: 10px;
 box-shadow: rgb(0 0 0 / 80%) 0px 40px 58px -16px,
    rgb(0 0 0 / 72%) 0px 30px 22px -10px;
 cursor: pointer;
 overflow: hidden;
 position: relative;
 transition: all 250ms cubic-bezier(0.25, 0.46, 0.45, 0.94) 0s;
 border: 3px solid rgba(249, 249, 249, 0.1);

 img{
    inset: 0;
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    opacity: 1;
    position: absolute;
    transition: opacity 500ms ease-in-out 0s;
    z-index: 1;
    top: 0;
 }

 &:hover {
    box-shadow: rgb(0 0 0 / 80%) 0px 40px 58px -16px,
    rgb(0 0 0 / 72%) 0px 30px 22px -10px;
    transform: scale(1.05);
    border-color: rgba(249, 249, 249, 0.8);
 }
`;

export default Recommends;